.aboutDiv {
  height: calc(100vh - 200px);
  width: 100vw;
  padding: calc(3vh + 70px) 0 3vh 0;
}

.aboutMe {
  border-radius: 5px;
  border: 0.5px solid gainsboro;
}

.aboutSection {
  height: calc(calc(100vh - 200px) - calc(6vh + 70px));
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.profileIconClass {
  margin-left: 5px;
}

.linksWidth {
  width: 150px;
}

@media (max-width:576px) {
  .aboutDiv {
    height: calc(103vh + 70px);
    padding: 0;
    padding-top: calc(3vh + 70px);
  }

  .aboutSection {
    height: 50vh
  }

  .smallScreenAvatar {
    width: 100px;
    height: 100px;
  }

  .aboutMe {
    border: none;
  }
}