.productDiv {
  height: fit-content;
  width: 100vw;
}

.productsHeading {
  margin-top: 20px;
  width: 100vw;
  text-align: center;
}

.products {
  display: flex;
  flex-wrap: wrap;
  padding: 0 6vmax;
  justify-content: center;
  min-height: 30vh;
}

.paginationBox {
  display: flex;
  justify-content: center;
}

.filterBox {
  display: block;
  width: 600px;
  margin: auto;
  padding-top: 100px;
  text-align: center;
}

.categoryBox {
  width: 100%;
  height: fit-content;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.category-link {
  list-style: none;
  color: rgba(0, 0, 0, 0.61);
  font: 400 0.8vmax "Roboto";
  margin: 0.4vmax;
  cursor: pointer;
}


@media screen and (max-width: 600px) {
  .filterBox {
    width: 100vw;
  }

  .category-link {
    list-style: none;
    color: rgba(0, 0, 0, 0.61);
    font: 400 1.3vmax "Roboto";
    margin: 0.4vmax;
    cursor: pointer;
  }
}