.searchBox {
  width: 100vw;
  height: calc(100vh - 200px);
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #E6E7ED;
}

.searchDiv {
  padding: 5px;
  background-color: white;
  border-radius: 7px;
  height: fit-content;
  width: fit-content;
}

.searchBox>div>input[type="text"] {
  height: 50px;
  width: 600px;
  border-radius: 7px;
  border: none;
  background-color: #E3E7F3;
  color: black;
  padding-left: 10px;
  font-weight: 600;
}

.searchBox>div>input[type="submit"] {
  margin: 0 10px;
  background-color: transparent;
  border: none;
  border-radius: 5px;
  height: 50px;
  width: 100px;
  transition: all 0.5s;
}

.searchBox>div>input[type="submit"]:hover {
  background-color: #E3E7F3;
}

@media screen and (max-width: 650px) {
  .searchBox {
    height: calc(100vh - 480px);
  }

  .searchBox>div>input[type="text"] {
    width: 60vw;
  }

  .searchBox>div>input[type="submit"] {
    margin: 0 5px;
    height: 50px;
    width: 90px;
  }
}

@media screen and (max-width: 375px) {
  .searchBox {
    height: calc(100vh - 380px);
  }
}