.dashboard {
  width: 100vw;
  padding-top: 50px;
  max-width: 100%;
  display: grid;
  grid-template-columns: 1fr 5fr;
}

.dashboardContainer {
  border-left: 1px solid rgba(0, 0, 0, 0.13);
  background-color: rgb(255, 255, 255);
  padding: 3rem 0;
}

.dashboardContainer>h1 {
  color: rgba(0, 0, 0, 0.733);
  font: 300 2rem "Roboto";
  text-align: center;
  width: 50%;
  padding: 1.5rem;
  margin: auto;
}

.dashboardSummary {
  margin: 2rem 0;
}

.dashboardSummary>div {
  display: flex;
  background-color: white;
  justify-content: center;
}

.dashboardSummary>div>p {
  background-color: #27ae60;
  color: white;
  font: 300 1.3rem "Roboto";
  text-align: center;
  padding: 1.5rem;
  width: 100%;
  margin: 0 2rem;
  border-radius: 5px;
}

.dashboardSummaryBox2>a {
  color: white;
  font: 300 2rem "Roboto";
  text-align: center;
  background-color: #2980b9;
  text-decoration: none;
  padding: 1.5rem;
  width: 10vmax;
  height: 10vmax;
  margin: 2rem;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.dashboardSummaryBox2>a:first-child {
  background-color: #e67e22;
  color: rgb(255, 255, 255);
}

.dashboardSummaryBox2>a:last-child {
  background-color: #2c3e50;
  color: rgb(255, 255, 255);
}

.lineChart {
  width: 80%;
  margin: auto;
}

.doughnutChart {
  width: 30vmax;
  margin: auto;
}

#adminActionBtn {
  display: none;
}

@media screen and (max-width: 600px) {
  .dashboard {
    grid-template-columns: 1fr;
  }

  .dashboardContainer {
    border-left: none;
  }

  .dashboardContainer h1 {
    display: none;
  }

  .dashboardSummary>div>p {
    margin: 0;
    border-radius: 0;
  }

  .dashboardSummaryBox2>a {
    padding: 0.5rem;
    margin: 1rem;
    font: 300 0.9rem "Roboto";
  }

  #adminActionBtn {
    display: block;
    margin: auto;
  }
}