.emptyCart {
  margin: auto;
  text-align: center;
  padding: 10vmax;
  width: 100vw;
  height: calc(100vh - 200px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.emptyCart>svg {
  font-size: 5vmax;
  color: #dc3545;
}

.emptyCart>p {
  font-size: 2vmax;
}

.emptyCart>a {
  background-color: rgb(51, 51, 51);
  color: white;
  border: none;
  padding: 1vmax 3vmax;
  cursor: pointer;
  font: 400 1vmax "Roboto";
  text-decoration: none;
}

.cartPage {
  padding-top: 100px;
  height: calc(100vh - 200px);
  width: 100vw;
  overflow: hidden;
  overflow-y: scroll;
}

.cartHeader {
  background-color: #dc3545;
  width: 90%;
  box-sizing: border-box;
  margin: auto;
  color: white;
  display: grid;
  grid-template-columns: 4fr 1fr 1fr;
  font: 300 0.7vmax "Roboto";


}

.cartHeader>p {
  margin: 10px;


}

.cartHeader>p:last-child {
  text-align: end;
}

.cartContainer {
  width: 90%;
  margin: auto;
  display: grid;
  grid-template-columns: 4fr 1fr 1fr;


}

.cartInput {
  display: flex;
  align-items: center;
  height: 8vmax;
  justify-content: center;
}

.cartInput>button {
  border: none;
  background-color: rgba(0, 0, 0, 0.616);
  padding: 0.5vmax;
  cursor: pointer;
  color: white;
  transition: all 0.5s;


}

.cartInput>button:hover {
  background-color: rgba(0, 0, 0, 0.767);
}

.cartInput>input {
  border: none;
  padding: 0.5vmax;
  width: 2vmax;
  text-align: center;
  outline: none;
  font: 400 0.8vmax "Roboto";
  color: rgba(0, 0, 0, 0.74);


}

.cartSubtotal {
  display: flex;
  padding: 0.5vmax;
  height: 8vmax;
  align-items: center;
  box-sizing: border-box;
  font: 300 1vmax;
  justify-content: flex-end;
  color: rgba(0, 0, 0, 0.753);


}

.cartGrossProfit {
  display: grid;
  grid-template-columns: 2fr 1.2fr;
}

.cartGrossProfitBox {
  border-top: 3px solid #dc3545;
  margin: 1vmax 4vmax;
  box-sizing: border-box;
  padding: 2vmax 0;
  font: 300 1vmax "Roboto";
  display: flex;
  justify-content: space-between;
}

.checkOutBtn {
  display: flex;
  justify-content: flex-end;
}

.checkOutBtn>button {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 0.8vmax 4vmax;
  width: 50%;
  font: 300 0.8vmax "Roboto";
  margin: 1vmax 4vmax;
  cursor: pointer;
  border-radius: 30px;
}

@media screen and (max-width: 600px) {
  .emptyCart {
    width: 100vw;
    height: calc(100vh - 480px);
  }

  .cartPage {
    height: 100vh;
  }

  .cartHeader {
    display: none;
  }

  .cartContainer {
    width: 100%;
    grid-template-columns: 1.5fr 1fr;
  }

  .cartInput {
    height: 20vmax;
  }

  .cartInput>button {
    padding: 1.5vmax;
  }

  .cartInput>input {
    width: 3vmax;
    padding: 0.5vmax;
    font: 400 1.8vmax "Roboto";
  }

  .cartSubtotal {
    display: none;
  }

  .cartGrossProfit {
    display: grid;
    grid-template-columns: 0fr 2fr;
  }

  .cartGrossProfitBox {
    padding: 2vmax;
    font: 300 2vmax "Roboto";
  }

  .checkOutBtn>button {
    padding: 1vmax 4vmax;
    width: 100%;
    font: 300 2vmax "Roboto";
  }
}