.darkBGColor {
  background-color: black;
}

.togglerBtn {
  color: aliceblue;
}

.navbarPosition {
  position: absolute;
  width: 100%;
  z-index: 100;
}

.banner {
  background-image: url("../../images/123660.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vmin;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
  color: white;
}

.banner::after {
  content: "";
  width: 100vw;
  height: 100vmin;
  background-color: #ffffff;
  position: absolute;
  top: 0%;
  left: 0;
  clip-path: polygon(100% 68%, 0 100%, 100% 100%);
  max-width: 100%;
}

.banner>p {
  font: 300 1.4vmax "Lucida Sans";
}

.banner>h1 {
  margin: 2vmax;
  font: 600 2.5vmax "Roboto";
}

.banner>a>button {
  margin-bottom: 5vmax;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid white;
  border-radius: 20px;
  padding: 10px;
  transition: all 0.5s;
  width: 7vmax;
  font: 500 1vmax "Roboto";
  color: #ffffff;
  transition: all 1s;
}

.banner>a>button:hover {
  background-color: white;
  color: black;
}

.homeHeading {
  text-align: center;
  font-family: Roboto;
  font-size: 1.4vmax;
  border-bottom: 1px solid black;
  width: 20vmax;
  padding: 1vmax;
  margin: 5vmax auto;
  color: black;
}

.containerProduct {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 80vw;
  margin: 2vmax auto;
  max-width: 100%;
  /* border: 1px solid black; */
}

.productCard {
  width: 14vmax;
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: rgb(48, 48, 48);
  margin: 2vmax;
  transition: all 0.5s;
  padding-bottom: 0.5vmax;
  box-shadow: 0 0 5px rgba(15, 15, 15, 0.26);
  border-radius: 5px;
}

.productCard>img {
  width: 14vmax;
  height: 15vmax;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.productCard>div {
  margin: 0.5vmax;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.productCardSpan {
  margin: 0.5vmax;
  font: 300 0.7vmax "Roboto";
}

.productCard>p {
  font-family: "Roboto";
  font-size: 1.2vmax;
  margin: 1vmax 0.5vmax;
  margin-bottom: 0;
}

.productCard>span {
  margin: 0.5vmax;
  /* color: tomato; */
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 1.5vmax;
  text-align: center;
  font-weight: 900;
}

.productCard:hover {
  box-shadow: 0 0 5px rgba(15, 15, 15, 0.26);

  transform: translateY(-1vmax);
}

@media screen and (max-width: 600px) {
  .containerProduct {
    width: 100vw;
  }

  .productCard {
    width: 15vmax;
    margin: 1vmax;
  }

  .productCard>img {
    width: 15vmax;
  }

  .productCard>p {
    font-size: 1.7vmax;
  }

  .productCard>div {
    margin: 0vmax;
    display: block;
  }

  .productCard>span {
    font-size: 1.5vmax;
  }

  .productCard>div>span {
    margin: 0 0.5vmax;
    font: 300 1vmax "Roboto";
  }
}