.orderDetailsPage {
  height: calc(100vh - 200px);
  width: 100vw;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.orderDetailsContainer {
  background-color: white;
  padding: 100px 1vw;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.orderDetailsContainer>h3 {
  color: #dc3545;
}

.orderDetailsContainer>h5 {
  color: #3498db;
}

.orderDetailsContainerBox {
  width: 100%;
}

.orderDetailsCartItems {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-left: 1px solid #bdc3c7;
}

.orderDetailsCartItemsContainer {
  width: 100%;
  text-align: center;
}

.orderDetailsCartItemsContainer img {
  height: 200px;
  display: block;
  margin: auto;
}

.orderDetailsCartItemsContainer a {
  display: block;
  text-decoration: none;
  color: #dc3545;
}

.orderDetailsCartItemsContainer span {
  display: block;
  color: #27ae60;
}


@media (max-width:600px) {
  .orderDetailsPage {
    height: fit-content;
    width: 100vw;
    display: grid;
    grid-template-columns: 1fr;
    padding-bottom: 50px;
  }

  .orderDetailsContainerBox p {
    text-align: center;
  }
}