.newProductContainer {
  width: 100%;
  box-sizing: border-box;
  background-color: rgb(221, 221, 221);
  border-left: 1px solid rgba(0, 0, 0, 0.158);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.newProductContainer h1 {
  color: rgba(0, 0, 0, 0.733);
  font: 300 1.5rem "Roboto";
  text-align: center;
}

.createProductForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 3vmax;
  justify-content: space-evenly;
  height: 70%;
  width: 60vh;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.267);
  border-radius: 5px;
}

.createProductForm>div {
  display: flex;
  width: 100%;
  align-items: center;
}

.createProductForm>div>input,
.createProductForm>div>select,
.createProductForm>div>textarea {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  font: 300 0.9vmax;
  outline: none;
}

.createProductForm>div>svg {
  position: absolute;
  transform: translateX(1vmax);
  font-size: 1.6vmax;
  color: rgba(0, 0, 0, 0.623);
}

#createProductFormFile>input {
  display: flex;
  padding: 0%;
}

#createProductFormFile>input::file-selector-button {
  cursor: pointer;
  width: 100%;
  z-index: 2;
  height: 5vh;
  border: none;
  margin: 0%;
  font: 400 0.8vmax;
  transition: all 0.5s;
  padding: 0 1vmax;
  color: rgba(0, 0, 0, 0.623);
  background-color: rgb(255, 255, 255);
}

#createProductFormFile>input::file-selector-button:hover {
  background-color: rgb(235, 235, 235);
}

#createProductFormImage {
  width: 100%;
  overflow: auto;
}

#createProductFormImage>img {
  width: 3vmax;
  margin: 0 0.5vmax;
}

#createProductBtn {
  border: none;
  background-color: #dc3545;
  color: white;
  font: 300 0.9vmax "Roboto";
  width: 100%;
  padding: 0.8vmax;
  cursor: pointer;
  transition: all 0.5s;
  border-radius: 4px;
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

#createProductBtn:hover {
  background-color: #9c222f;
}

@media screen and (max-width: 700px) {
  .newProductContainer {
    background-color: rgb(255, 255, 255);
  }

  .createProductForm {
    width: 90vw;
  }

  .createProductForm>div>input,
  .createProductForm>div>select,
  .createProductForm>div>textarea {
    padding: 2.5vmax 5vmax;
    font: 300 1.7vmax;
  }

  .createProductForm>div>svg {
    font-size: 2.8vmax;
  }

  #createProductFormFile>img {
    width: 8vmax;
    border-radius: 100%;
  }

  #createProductFormFile>input::file-selector-button {
    height: 7vh;
    font: 400 1.8vmax;
  }

  #createProductBtn {
    font: 300 1.9vmax "Roboto";
    padding: 1.8vmax;
  }
}