.sidebar {
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  align-items: start;
  padding: 4rem 0;
}

.sidebar a {
  text-decoration: none;
  color: black;
  font: 200 1rem "Roboto";
  padding: 2rem 0;
  transition: all 0.5s;
}

.sidebar a:hover {
  color: #dc3545;
  transform: scale(1.1);
}

.sidebar a>P {
  display: flex;
  align-items: center;
}

.sidebar a>p>svg {
  margin-right: 0.5rem;
}

.MuiTypography-root {
  background-color: #fff !important;
}

@media (max-width:600px) {
  #sidebar {
    display: none;
  }

  .mobileSidebar {
    height: 300px;
    width: 100%;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
  }

  .mobileSidebar a {
    margin: 0;
    font-size: 1rem;
    color: #3498db;
    text-decoration: none;
    width: 100%;
    display: block;
    /* border: 1px solid black; */
    /* text-align: center; */
  }
}