.LoginSignUpContainer {
  width: 100vw;
  height: calc(100vh - 200px);
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #E6E7ED;
  padding-top: 70px;
}

.LoginSignUpBox {
  background-color: white;
  width: 400px;
  height: 80%;
  overflow: hidden;
  border-radius: 5px;
}

.login_signUp_toggle {
  display: flex;
  height: 1.5vmax;
  margin-top: 10px;
}

.login_signUp_toggle>p {
  color: rgba(0, 0, 0, 0.678);
  font: 300 1vmax "Roboto";
  transition: all 0.5s;
  cursor: pointer;
  display: grid;
  place-items: center;
  width: 100%;
}

.login_signUp_toggle>p:hover {
  color: #dc3545;
}

.LoginSignUpBox>div>button {
  background-color: #E3E7F3;
  height: 3px;
  width: 50%;
  border: none;
  transition: all 0.5s;
  font-weight: 600;
}

.loginForm,
.signUpForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 1vmax;
  justify-content: space-evenly;
  height: 70%;
  transition: all 0.5s;
}

.signUpForm {
  transform: translateY(-100%) translateX(-100vmax);
}

.loginForm>div,
.signUpForm>div {
  display: flex;
  width: 100%;
  align-items: center;
}

.loginInput {
  width: 100%;
  display: block;
  margin: auto;
}


.loginForm>a {
  color: #565d7b;
  text-decoration: none;
  align-self: flex-end;
  transition: all 0.5s;
  font: 500 0.8vmax "Gill Sans";
}

.loginForm>a:hover {
  color: black;
}

#registerImage>img {
  width: 3vmax;
  border-radius: 100%;
}

#registerImage>input {
  display: flex;
  padding: 0%;
  height: 50px;
  width: 90%;
  background-color: #9197b4;
  color: black;
}

#registerImage>input::file-selector-button {
  cursor: pointer;
  width: 100%;
  z-index: 2;
  height: 5vh;
  border: none;
  margin: 0%;
  font: 400 0.8vmax;
  transition: all 0.5s;
  padding: 0 1vmax;
  color: rgba(0, 0, 0, 0.623);
  background-color: rgb(255, 255, 255);
}

#registerImage>input::file-selector-button:hover {
  /* background-color: rgb(235, 235, 235); */
  cursor: pointer;
}

.loginBtn,
.signUpBtn {
  width: 100%;
}

.loginBtn:hover,
.signUpBtn:hover {
  background-color: #9f2834;
}

.shiftToLeft {
  transform: translateX(-100%);
}

.shiftToNeutral {
  transform: translateX(0%);
}

.shiftToNeutralForm {
  transform: translateX(0%) translateY(-100%);
}

.shiftToRight {
  transform: translateX(100%);
}

@media screen and (max-width: 600px) {
  .LoginSignUpContainer {
    background-color: white;
  }

  .LoginSignUpBox {
    width: 100vw;
    height: 95vh;
  }

  .login_signUp_toggle {
    height: 5vmax;
  }

  .login_signUp_toggle>p {
    font: 300 1.5vmax "Roboto";
  }

  .loginForm,
  .signUpForm {
    padding: 5vmax;
  }

  .loginForm>div>input,
  .signUpForm>div>input {
    padding: 2.5vmax 5vmax;
    font: 300 1.7vmax cursive;
  }

  .loginForm>div>svg,
  .signUpForm>div>svg {
    font-size: 2.8vmax;
  }

  .loginForm>a {
    font: 500 1.8vmax "Gill Sans";
  }

  #registerImage>img {
    width: 8vmax;
    border-radius: 100%;
  }

  #registerImage>input::file-selector-button {
    height: 7vh;
    font: 400 1.8vmax cursive;
  }

  .loginBtn,
  .signUpBtn {
    font: 300 1.9vmax "Roboto";
    padding: 1.8vmax;
  }
}