.shippingContainer {
  width: 100vw;
  min-height: calc(100vh - 200px);
  padding: 100px 0;
  overflow: hidden;
  overflow-y: scroll;
}

.shippingBox {
  height: 650px;
  width: 550px;
  margin: auto;
  display: grid;
  grid-template-rows: 1fr 10fr;
}

.shippingHeading {
  text-align: center;
  color: #dc3545;
}

.shippingForm {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.shippingForm input {
  width: 400px;
}

@media (max-width:600px) {
  .shippingContainer {
    min-height: calc(100vh - 480px);
  }

  .shippingBox {
    height: 650px;
    width: 90vw;
  }

  .shippingForm input {
    width: 80vw;
  }
}