.confirmOrderPage {
  height: calc(100vh - 200px);
  width: 100vw;
  overflow: hidden;
  overflow-y: scroll;
  padding-top: 80px;
}

.confirmForm {
  width: 100vw;
  height: calc(100vh - 395px);
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.confirmForm>div {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.confirmshippingArea h5 {
  text-align: center;
}

.confirmshippingAreaBox p {
  width: 100%;
  text-align: center;
}


.confirmCartItems h5 {
  text-align: center;
}

.confirmCartItemsContainer {
  text-align: center;
}

.confirmCartItemsContainer img {
  height: 100px;
}

.confirmCartItemsContainer a {
  font-size: 1.5rem;
  text-decoration: none;
  color: #dc3545;
}

.orderSummary {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.orderSummaryTotal p {
  font-size: 1.5rem;
  color: #dc3545;
}

@media (max-width:600px) {
  .confirmOrderPage {
    height: 1000px;
  }

  .confirmForm {
    height: calc(700px);
    grid-template-columns: 1fr;
  }

  .confirmCartItemsContainer span {
    display: block;
  }
}