.paymentContainer {
  width: 100vw;
  height: calc(100vh - 200px);
  padding-top: 80px;
}

.paymentForm {
  height: 400px;
  width: 400px;
  margin: auto;
}

.paymentForm h5 {
  text-align: center;
}

.paymentForm>div {
  display: flex;
  align-items: center;
  margin: 2vmax 0;
}

.paymentInput {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  outline: none;
}

.paymentForm>div>svg {
  position: absolute;
  transform: translateX(1vmax);
  font-size: 1.6vmax;
  color: rgba(0, 0, 0, 0.623);
}

.paymentFormBtn {
  width: 100%;
}

@media (max-width:600px) {
  .paymentContainer {
    height: fit-content;
    padding-top: 70px;
  }

  .paymentForm {
    height: fit-content;
    width: 90vw;
    padding-bottom: 50px;
  }
}